@import url(https://fonts.googleapis.com/css?family=Old+Standard+TT&display=swap);
body {
    margin: 0;
    /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif; */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

body {
    background: #e0e1e6;
    font-family: "Old Standard TT", serif;
}
.App {
    position: relative;
}

.site-header {
    height: 50px;
    display: grid;
    justify-items: center;
    align-items: center;
    background-color: transparent;
    width: 100%;
    color: #fff;
    font-size: 2em;
    position: fixed;
    top: 0;
    z-index: 100;
    -webkit-transition: background-color 0.5s ease;
    transition: background-color 0.5s ease;
}
.site-header.color {
    background-color: #2e4057;
    -webkit-transition: background-color 0.3s ease;
    transition: background-color 0.3s ease;
}
.site-header a {
    padding-top: 5px;
}
.site-header-logo {
    width: 40px;
    height: 40px;
    z-index: 100;
}

.searcher {
    z-index: 4;
    min-height: calc(100vh - 50px);
    position: relative;
    display: grid;
    grid-template-columns: 1fr 80% 1fr;
    grid-template-rows: 150px -webkit-min-content auto;
    grid-template-rows: 150px min-content auto;
    grid-row-gap: 50px;
    grid-template-areas:
        ". . ."
        ". container ."
        "motvlist motvlist motvlist";
}

.searcher-container {
    grid-area: container;
    background: #fff;
    box-shadow: 0px 0px 5px 0px rgba(98, 150, 155, 0.75);
    border-radius: 2px;
    padding: 35px;
    font-size: 1.8rem;
    position: relative;
}
.searcher-container-label {
    display: block;
}
.searcher-container-input {
    width: 89%;
    padding: 11px;
    font-size: 1.5rem;
    display: inline-block;
    border: 1px solid black;
    font-family: inherit;
}
button {
    font-size: 1.5rem;
    padding: 11px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: #fff;
    border: none;
    cursor: pointer;
    outline: none;
    font-family: inherit;
}
.selector-buttons {
    position: absolute;
    top: -50px;
    left: 0;
}
.searcher-container-button {
    display: inline-block;
    width: 100%;
    border: 1px solid black;
    /* cursor: pointer; */
    /* font-size: 1.5rem;
    padding: 11px;
    appearance: none;
    background: #fff; */
}
.selector-buttons-movie {
    /* border: 1px solid black; */
    border-right: 1px solid black;
    background: #d7d6d6;
    /* cursor: pointer; */
}
.selector-buttons-tv {
    /* border: 1px solid black; */
    border-bottom: none;
    border-left: none;
    background: #d7d6d6;
    /* cursor: pointer; */
}
.active {
    background: white;
}
@media (min-width: 1024px) {
    .searcher {
        grid-template-columns: 3fr 40% 10fr;
    }
    .searcher-container {
        font-size: 3rem;
    }
    .searcher-container-input {
        width: 70%;
        padding: 11px;
    }
    .selector-buttons {
        top: -62px;
    }
    .searcher-container-button {
        width: 24%;
    }
}

.MOTV-list {
    display: grid;
    grid-area: motvlist;
    /* grid-template-columns: auto; */
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
       -moz-column-gap: 10px;
            column-gap: 10px;
    grid-row-gap: 10px;
    row-gap: 10px;
    padding: 15px;
    /* margin: 10px; */
    background: white;
    border-radius: 3px;
    box-shadow: 0px 0px 15px 5px rgba(184, 181, 181, 0.75);
    margin: 10px 35px;
    /* height: 100%; */
}

.MOTV-list a {
    text-decoration: none;
}
.MOTV-list-error {
    grid-area: motvlist;
    background: white;
    border-radius: 3px;
    box-shadow: 0px 0px 15px 5px rgba(184, 181, 181, 0.75);
    margin: 30px 20px;
    padding: 20px;
    text-align: center;
}

/* @media (min-width: 1024px) {
    .MOTV-list {
        grid-template-columns: repeat(8, 1fr);
    }
} */

.MOTV-list-item {
    justify-self: center;
    padding: 3px;
    border: 2px solid transparent;
    color: black;
}
.MOTV-list-item:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    cursor: pointer;
    border: 2px solid black;
    border-radius: 10px;
}
.MOTV-list-item-img {
    border-radius: 10px;
    display: inline-block;
    width: 100%;
}
.MOTV-list-item-title {
    text-align: center;
}

.site-image {
    position: absolute;
    /* top: 0; */
    z-index: 1;
}

.title-info {
    display: grid;
    grid-template-rows: 500px -webkit-min-content;
    grid-template-rows: 500px min-content;
}
.title-info-rest {
    display: grid;
    grid-template-columns: 30% 70%;
    position: relative;
    padding: 20px 0 30px 0;
}

.title-info-poster {
    position: absolute;
    top: -45px;
    padding: 0 25px 25px;
    /* display: block; */
    position: relative;
    opacity: 1;
    z-index: 10;
}
.title-info-poster-img {
    display: block;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    width: 100%;
}
.title-info-backdrop {
    position: relative;
}
.title-info-backdrop::after {
    content: "";
    height: 100px;
    position: absolute;
    bottom: 0;
    left: 0;
    background-image: -webkit-gradient(
        linear,
        left top, left bottom,
        from(rgba(255, 255, 255, 0)),
        to(rgba(255, 255, 255, 0.4))
    );
    background-image: -webkit-linear-gradient(
        top,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.4) 100%
    );
    background-image: linear-gradient(
        to bottom,
        rgba(255, 255, 255, 0) 0%,
        rgba(255, 255, 255, 0.4) 100%
    );
    width: 100%;
}
.title-info-desc {
    font-size: 1rem;
}
.title-info-desc-title {
    font-size: 1em;
}

@media (min-width: 1024px) {
    .title-info-desc {
        font-size: 2rem;
    }
    .title-info-desc-title {
        font-size: 2em;
    }
}

